/**
 * bot-test.js
 */

require('./bootstrap');

import BS4Alert from './components/alert'

let mainVue = new Vue({
    el: '#vue-main',
    data: {
        payload: '',
        targetUrl: 'main.php',
        isSending: false,
        result: '',
        alertShow: ''
    }, 
    components: {
        'bs4-alert': BS4Alert
    },
    methods: {
        clear_log: function () {
            this.payload    = ''
            this.alertShow  = ''
        },
        test_log: function () {
            /* clear alert and enable spinner */
            this.isSending = true
            this.alertShow = ''

            /* setup API url and request payload */
            let url = '/tg/bots/' + this.targetUrl
            let data = this.payload

            /* trying to prase payload into JSON format */
            try {
                data = JSON.parse(data);
            } catch (e) {
                this.isSending = false
                this.alertShow = 'warning'
                return
            }
            data = JSON.stringify(data);
        
            /* if format corrently, fetch API */
            fetch( url, {
                method: 'POST', 
                body: data, 
                headers: new Headers({
                    'Content-Type': 'application/json'
                })
            }).then( (res) => { 
                if(res.ok) {
                    return res.text();
                }
                let err = new Error('Network response was not ok.')
                err.status = res.status
                throw err
            }).then( (res) => { 
                this.isSending  = false
                this.result     = res;
                this.alertShow  = 'success'
            }).catch( (err) => {
                this.isSending  = false
                this.result     = err.status
                this.alertShow  = 'danger'
            })
        },
        getURL: () => {
            let url = window.location.href
            let urlArr = url.split('?')
            let getDict = {}
    
            if (urlArr.length <= 1) {
                return getDict
            }

            urlArr[1].split('&').forEach( (element) => {
                let values = element.split('=')
                if (values.length > 1) {
                    getDict[values[0]] = values[1]
                }
            })
    
            return getDict
        }
    },
    mounted: function () {
        let gets = this.getURL()
        if (Object.keys(gets).length > 0 && gets.uid) {
            let uid = gets.uid
            window.axiosInstance.get(`log/update/${uid}`)
            .then( (res) => {
                this.payload = JSON.stringify(res.data.result[0], null, '\t')
            })
        }
    }
})
